@charset "UTF-8";

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  text-decoration: inherit;
  color: inherit;
}

ul li {
  list-style: none;
}

@font-face {
  font-family: "MYRIADPRO-BOLD";
  font-display: swap;
  src: url("../fonts/MYRIADPRO-BOLD.woff") format("woff"), url("../fonts/MYRIADPRO-BOLD.woff2") format("woff2");
  font-weight: "700";
  font-style: "normal";
}

