  font-family: "MYRIADPRO";
  font-display: swap;
  src: url("../fonts/MYRIADPRO-REGULAR.woff") format("woff"), url("../fonts/MYRIADPRO-REGULAR.woff2") format("woff2");
  font-weight: "400";
  font-style: "normal";
}

