.search-bar {
  height: 40px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  position: relative;
}

.search-bar--active .search-bar__submit {
  display: block;
}

.search-bar--active .search-bar__input {
  margin-left: 10px;
  width: 167px;
}

.search-bar__submit {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 21px;
  height: 21px;
  z-index: 1;
  background: transparent;
  cursor: pointer;
}

.search-bar__icon {
  cursor: pointer;
}

.search-bar__input {
  width: 0;
  color: #f9f9f9;
  background: transparent;
  transition: width 0.2s;
}

.sidebar ::-webkit-scrollbar,
.product ::-webkit-scrollbar {
  width: 5px;
}

.sidebar ::-webkit-scrollbar-thumb,
.product ::-webkit-scrollbar-thumb {
  background: #bfbfbf;
  border-radius: 3px;
}

.sidebar ::-webkit-scrollbar-track,
.product ::-webkit-scrollbar-track {
  background: #1b1a1a;
  width: 1px;
  border: 2px solid transparent;
  background-clip: padding-box;
}

.sidebar ::-webkit-scrollbar-thumb:hover,
.product ::-webkit-scrollbar-thumb:hover {
  background: #aaa;
}

.discount {
  width: 80px;
  height: 80px;
  overflow: hidden;
  position: absolute;
  z-index: 10;
  top: 0;
  right: 0px;
}

.discount div {
  position: absolute;
  width: 112px;
  padding: 5px 0;
  background-color: #da0b29;
  text-align: center;
  left: -5px;
  top: 15px;
  transform: rotate(45deg);
  color: #f9f9f9;
  font-family: 'Verdana', sans-serif;
  font-size: 16px;
  font-weight: 700;
}

.header {
  font-family: 'Verdana';
  font-size: 14px;
  background-color: #1b1a1a;
}

.header__logo {
  margin-right: auto;
}

.header__container {
  display: flex;
  max-width: 1150px;
  height: 75px;
  margin: 0 auto;
  padding: 0 10px;
  align-items: center;
  border-bottom: 1px solid #212020;
}

.header__container .icon-menu {
  display: none;
  transform: scaleX(-1);
  width: 30px;
  height: 30px;
  transition: all 0.5s;
}

.header__container .icon-menu--opened {
  transform: scaleX(1);
}

.header__button {
  display: inline-flex;
  align-items: center;
  color: #bfbfbf;
  font-weight: 700;
}

.header__button--search {
  margin-right: 36px;
}

.header__button .svg-icon {
  margin-right: 8px;
}

.header__sign {
  margin-left: 24px;
  height: 100%;
  transition: left 0.5s;
}

.logo-header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 234px;
  height: 100%;
  background-color: #da0b29;
}

.logo-header:hover .logo-header__icon {
  filter: drop-shadow(0px 0px 5px #fff);
}

.logo-header__icon {
  margin-right: 12px;
}

.logo-header__text {
  font-family: 'MyriadPro';
  color: #f9f9f9;
  font-size: 16px;
}

.logo-header__title {
  font-weight: 700;
  font-size: 24px;
}

.main-icon {
  height: 50px;
  width: 50px;
  display: inline-block;
  background: url("../img/main-logo.svg") no-repeat;
}

.sign {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
  padding: 0 10px;
  color: #bfbfbf;
}

.sign--mobile {
  display: none;
  color: #f9f9f9;
}

.sign--mobile .sign__title {
  font-size: 16px;
  text-align: center;
  margin-top: 120px;
}

.sign--mobile .sign__subtitle {
  font-size: 12px;
  left: 42%;
  position: relative;
}

.sign__type {
  cursor: pointer;
}

.sign__type--sign-in {
  margin-right: 5px;
}

.sign__methods {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.sign__methods .svg-icon {
  margin: 0 3px;
}

.sign-form__item {
  margin: 15px 20px;
}

.sign-form__item:last-child {
  margin-top: 30px;
  text-align: center;
}

.sign-form input[type='checkbox'] {
  display: none;
}

.sign-form input[type='checkbox']:checked + .fake-checkbox::before {
  opacity: 1;
}

.sign-form input[type='checkbox']:checked,
.sign-form input[type='checkbox']:checked ~ span {
  color: #f9f9f9;
  transition: color 0.2s;
}

.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  padding: 0 65px 0 65px;
  position: relative;
}

.navigation__list {
  display: flex;
  white-space: nowrap;
  overflow: hidden;
  color: #f0f0f0;
}

.navigation__list .svg-icon {
  margin-right: 8px;
}

.navigation__link {
  display: inline-flex;
  align-items: center;
}

.navigation__link:last-child {
  margin-right: 35px;
}

.sidebar--filter,
.sidebar--nav {
  left: 0;
  width: 320px;
  border-right: 1px solid #1b1a1a;
}

.sidebar--filter.sidebar--hidden,
.sidebar--nav.sidebar--hidden {
  transform: translateX(-100%);
}

.sidebar--nav {
  display: none;
}

.sidebar--nav .sidebar-list__item {
  border-bottom: 0;
  padding: 15px 0 15px 15px;
}

.sidebar--nav .sidebar-list__link {
  display: flex;
  align-items: center;
  color: #f9f9f9;
}

.sidebar--nav .sidebar-list__link .svg-icon {
  margin-right: 5px;
}

.fake-checkbox {
  display: inline-block;
  width: 10px;
  height: 10px;
  border: 1px solid #f9f9f9;
  border-radius: 5px;
  position: relative;
  margin-right: 5px;
}

.fake-checkbox::before {
  content: '';
  position: absolute;
  display: block;
  top: 1px;
  left: 1px;
  width: 6px;
  height: 6px;
  background-color: #f9f9f9;
  border-radius: 3px;
  opacity: 0;
  transition: opacity 0.2s;
}

.filter {
  font-size: 14px;
  font-weight: 400;
}

.filter label {
  cursor: pointer;
}

.filter__icon {
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.2s;
}

.filter__icon--arrow {
  width: 10px;
  height: 9px;
  background-image: url(../img/filter/filter__arrow.svg);
  right: -15px;
}

.filter__item--disabled {
  color: #807979;
}

.filter__item--disabled label {
  cursor: default;
}

.filter__item--disabled .fake-checkbox {
  border-color: #807979;
}

.filter__checkbox {
  display: none;
}

.filter__checkbox:checked + .fake-checkbox::before {
  opacity: 1;
}

.filter__checkbox:checked,
.filter__checkbox:checked ~ span {
  color: #f9f9f9;
  transition: color 0.2s;
}

.filter__name {
  display: inline-flex;
  align-items: center;
  padding: 13px 0;
  font-weight: 700;
  width: calc(100% - 25px);
  position: relative;
  transition: color 0.2s;
  min-height: 47px;
}

.filter__name--active {
  color: #f9f9f9;
}

.filter__name--active .filter__icon_arrow {
  transform: translateY(-50%) rotate(180deg);
}

.filter__name .svg-icon {
  flex-shrink: 0;
}

.filter__name .icon-square-box {
  margin-right: 5px;
}

.filter__name .icon-cancel-filter {
  margin-left: 10px;
}

.filter__items-wrapper {
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s;
}

.filter__items-wrapper .filter__items-list {
  padding-left: 15px;
}

.filter__items-list {
  padding: 18px 0;
}

.filter__item:not(:last-of-type) {
  margin-bottom: 18px;
}

.sidebar-list {
  height: calc(100% - 74px);
  overflow-y: auto;
  padding-left: 10px;
  padding-right: 5px;
}

.sidebar-list__item {
  padding-left: 15px;
  border-bottom: 1px solid #1b1a1a;
}

.sidebar--assemblage {
  right: 0;
  width: 360px;
  border-left: 1px solid #1b1a1a;
}

.sidebar--assemblage .sidebar__title {
  padding-top: 30px;
  padding-right: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sidebar--assemblage.sidebar--hidden {
  transform: translateX(100%);
}

.sidebar--assemblage .assemblage-items {
  padding: 0 0 15px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: auto;
  height: calc(100% - 81px);
}

.sidebar--assemblage .assemblage-items__footer {
  margin: 0 15px 0 20px;
}

.sidebar--assemblage .assemblage-items__info {
  display: flex;
  justify-content: space-between;
}

.sidebar--assemblage .assemblage-items__name {
  font-size: 14px;
  color: #807979;
  background: 0 0;
  border-bottom: 1px solid #1b1a1a;
  margin-right: 30px;
  flex-grow: 1;
  padding-bottom: 3px;
}

.sidebar--assemblage .assemblage-items__total {
  font-size: 12px;
  color: #afafaf;
}

.sidebar--assemblage .assemblage-items__pages {
  margin-top: 15px;
  position: relative;
}

.sidebar--assemblage .assemblage-items__pages .icon-steam {
  position: absolute;
  right: 0;
  top: 0;
}

.sidebar--assemblage .assemblage-items__pages .icon-vk {
  position: absolute;
  right: 26px;
  top: 0;
}

.pc-parts {
  padding: 0 30px;
  position: relative;
}

.pc-parts__item {
  padding: 10px 0 15px 30px;
  font-size: 12px;
  border-bottom: 1px solid #1b1a1a;
}

.pc-parts__item:not(.pc-part--current) {
  cursor: pointer;
}

.pc-parts__item .pc-part--multiple:not(:first-child) {
  margin-top: 15px;
}

.pc-parts__add-item {
  margin-top: 12px;
  color: #bfbfbf;
}

.pc-parts__add-item:before {
  content: '+ ';
  font-size: 18px;
  font-weight: 700;
}

.pc-parts__choose-item {
  color: #bfbfbf;
  font-size: 14px;
  font-weight: 700;
}

.pc-parts__choose-item:before {
  content: '+ ';
  font-size: 18px;
  font-weight: 700;
}

.pc-parts__choose-item[data-icon] {
  position: relative;
}

.pc-parts__choose-item[data-icon] .svg-icon {
  filter: url(#white) url(#red);
  position: absolute;
  left: -5px;
  top: 50%;
  transform: translateY(-50%);
}

.pc-part {
  display: flex;
}

.pc-part:hover .pc-part__cancel {
  display: block;
}

.pc-part--current {
  font-size: 14px;
  color: #807979;
  position: relative;
}

.pc-part__description {
  margin-right: auto;
  position: relative;
}

.pc-part__title {
  display: flex;
  align-items: center;
}

.pc-part__title p {
  color: #da0b29;
  white-space: nowrap;
  max-width: 215px;
  position: relative;
  overflow-x: hidden;
  text-overflow: ellipsis;
  backface-visibility: hidden;
}

.pc-part__title--gradient p:after {
  content: '';
  width: 25px;
  height: 100%;
  position: absolute;
  right: 0;
  background: linear-gradient(to left, #212020 20%, rgba(33, 33, 33, 0) 80%);
}

.pc-part__details {
  color: #807979;
  max-width: 215px;
  overflow-x: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  position: relative;
}

.pc-part__details--gradient:after {
  content: '';
  width: 25px;
  height: 100%;
  position: absolute;
  right: 0;
  background: linear-gradient(to left, #212020 20%, rgba(33, 33, 33, 0) 80%);
}

.pc-part__price {
  color: #bfbfbf;
  font-size: 11px;
  position: relative;
}

.pc-part__price:after {
  content: '₽';
  position: absolute;
  right: -10px;
}

.pc-part:hover .pc-part__price {
  color: #f9f9f9;
}

.pc-part[data-conflict-id] .pc-part__title {
  position: relative;
}

[data-icon] .pc-part__title,
.pc-part--current {
  position: relative;
}

[data-icon] .pc-part__title .svg-icon,
.pc-part--current .svg-icon {
  filter: url(#white) url(#red);
  position: absolute;
  left: -36px;
  top: 50%;
  transform: translateY(-50%);
}

.pc-part--current .svg-icon {
  left: -6px;
}

.pc-part__conflict-circle {
  position: absolute;
  left: -48px;
}

.pc-part__current-icon {
  display: flex;
  width: 14px;
  justify-content: space-between;
  align-items: center;
  margin-right: 8px;
}

.pc-part__current-icon span {
  display: inline-block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #807979;
}

.pc-part__cancel {
  display: none;
  position: absolute;
  top: 50%;
  right: -21px;
  transform: translateY(-50%);
}

.conflict-circle {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #e0a006;
  cursor: pointer;
}

.conflict-circle--hover,
.conflict-circle--fixed {
  transform: scale(1.25);
}

.conflict-circle--fixed--prev {
  transform: none;
}

.conflicts__title {
  width: 320px;
  margin-bottom: 10px;
  padding-left: 15px;
  line-height: 35px;
  font-size: 16px;
  font-weight: 700;
  color: #212020;
  background-color: #e0a006;
  border-radius: 3px;
}

.conflicts__item {
  font-size: 14px;
  color: #e0a006;
  padding: 5px 10px;
  cursor: pointer;
}

.conflicts__item:not(:last-child) {
  margin-bottom: 5px;
}

.conflicts__item:before {
  content: '! ';
  font-size: 16px;
  font-weight: 700;
}

.conflicts__item--chosen:not(.conflicts__item--prev),
.conflicts__item--hover:not(.conflicts__item--prev) {
  color: #212020;
  background-color: #e0a006;
  border-radius: 3px;
}

.conflicts__item--question {
  color: #bfbfbf;
}

.conflicts__item--question:before {
  content: '? ';
}

