.product-modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
}

.product-modal__close {
  color: white;
  position: absolute;
  top: 10px;
  right: 25px;
  font-size: 35px;
  font-weight: bold;
}

.product-modal__close:hover,
.product-modal__close:focus {
  color: #999;
  cursor: pointer;
}

.product-modal__content {
  width: 90%;
  max-width: 1200px;
  margin: auto;
  position: relative;
}

.product-modal__img {
  display: none;
  justify-content: center;
  align-items: center;
}

.product-modal__img img {
  max-width: 100%;
  max-height: 100%;
}

.product-modal__prev,
.product-modal__next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 20px;
  user-select: none;
  background-color: rgba(0, 0, 0, 0.8);
}

.product-modal__prev {
