.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  max-width: 1060px;
  margin: 24px auto 0;
  padding: 0 10px;
  box-sizing: content-box;
  font-family: Verdana, sans-serif;
  font-weight: 700;
}

.container--full-width {
  max-width: 100%;
}

.container--full-width .section__header {
  max-width: 1060px;
  margin: 0 auto 20px;
}

.container--full-width .section__products {
  max-width: 100%;
}

.main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.product--short {
  width: 340px;
  height: 450px;
}

.product--short .product__wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.product--short .product__assembly-score {
  font-size: 14px;
  color: #807979;
  font-weight: 700;
  position: absolute;
  top: 15px;
  left: 20px;
  z-index: 1;
}

.product--short .product__image-wrapper {
  width: 100%;
}

.product--short .product__title--overflow .product__name:after {
  content: '...';
}

.product--short .product__title .svg-icon {
  height: 14px;
  width: 14px;
  filter: url(#white);
}

.product--short .product__title .product__author {
  font-size: 14px;
  color: #807979;
}

.product--short .product__main-part {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.product--short .product__description {
  font-size: 14px;
  color: #bfbfbf;
  margin-top: 14px;
}

.product--short .product__price-and-buttons {
  flex-grow: 1;
  align-items: flex-end;
}

.section-slider {
  position: relative;
}

.section-slider:after,
.section-slider:before {
  content: '';
  width: 50px;
  height: 100%;
  position: absolute;
  z-index: 35;
  top: 0;
  transition: opacity 0.2s;
  pointer-events: none;
}

.section-slider:after {
  right: 0;
  background: linear-gradient(to left, #222222 20%, rgba(33, 33, 33, 0) 100%);
}

.section-slider:before {
  left: 0;
  background: linear-gradient(to right, #222222 20%, rgba(33, 33, 33, 0) 100%);
}

.section-slider--reach-end:after {
  opacity: 0;
}

.section-slider--reach-beg:before {
  opacity: 0;
}

.section-slider__btn {
  width: 40px;
  height: 40px;
  border-radius: 3px;
  background-color: #212020;
  border: 1px solid #1b1a1a;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 25;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.section-slider__btn--disabled {
  opacity: 0;
  pointer-events: none;
}

.section-slider__btn--next {
  right: 0px;
}

.section-slider__btn--next .svg-icon {
  transform: rotate(180deg);
}

.section-slider__btn--prev {
  left: 0px;
}

.section-slider__btn--toggle {
  top: unset;
  transform: none;
  right: 0px;
  bottom: 0;
}

.section-slider__btn .svg-icon {
  filter: url(#white) url(#grey);
}

.slider-container .swiper-container {
  max-width: 950px;
}

.slider-container .swiper-slide {
  margin: 10px;
}

.slider-container .product__description {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.slider-container--opened {
  padding: 0;
}

.slider-container--opened .section-slider {
  margin: -10px;
  max-width: 1080px;
}

.slider-container--opened .section-slider:after,
.slider-container--opened .section-slider:before {
  display: none;
}

.slider-container--opened .section-slider .swiper-wrapper {
  flex-wrap: wrap;
  justify-content: center;
}

.slider-container--opened .section-slider__btn--toggle .svg-icon {
  transform: rotate(180deg);
}

.slider-container--opened .section-slider__btn:not(.section-slider__btn--toggle) {
  display: none;
}

@media (max-width: 1625px) {
  .navigation {
    display: none;
  }

  .sidebar--nav {
    display: block;
  }
}

@media (max-width: 950px) {
  .footer {
    justify-content: center;
  }

  .footer__item:first-of-type {
    margin-bottom: 14px;
  }
}

@media (max-width: 768px) {
  .header__container .icon-menu {
    display: inline-block;
  }

  .header__buttons {
    position: fixed;
    left: -260px;
    top: 0;
    background-color: #1b1a1a;
    bottom: 0;
    z-index: 100;
    width: 260px;
  }

  .header__sign {
    position: fixed;
    margin: 0;
    padding: 10px;
    width: 260px;
    top: 0;
    left: -260px;
    bottom: 0;
    background-color: #1b1a1a;
    z-index: 100;
  }

  .header__sign--opened {
    left: 0;
  }

  .sign--desktop {
    display: none;
  }

  .sign--mobile {
    display: block;
  }
}

@media (max-width: 425px) {
  .section__product {
    margin: 5px 0 5px 0;
  }

  .section__slider-container {
    padding: 0;
    margin-bottom: 50px;
    margin: -5px -5px 50px -5px;
  }

  .product__button--big {
    width: 25px;
  }

  .product__button--big span:nth-of-type(2) {
    display: none;
  }

  .footer__logo {
    margin: 20px 0 20px 0;
  }

  .about-block {
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }

  .container {
    padding: 0 5px;
  }

  .section-slider:after,
  .section-slider:before {
    width: 5px;
  }

  .section-slider:after {
    background: linear-gradient(to left, #222222 0%, rgba(33, 33, 33, 0) 100%);
  }

  .section-slider:before {
    background: linear-gradient(to right, #222222 20%, rgba(33, 33, 33, 0) 100%);
  }

  .section-slider__btn {
    top: unset;
    bottom: -50px;
    transform: unset;
  }

  .section-slider__btn--next {
    right: 10px;
  }

  .section-slider__btn--prev {
    left: 10px;
  }

  .section-slider__btn--toggle {
    left: 50%;
    transform: translateX(-50%);
  }

  .slider-container .swiper-slide.product--short {
    margin: 5px;
  }

  .slider-container .swiper-slide.product--short .product__rating {
    margin-right: 10px;
  }

  .slider-container .swiper-slide.product--short .product__price {
    flex-shrink: 0;
  }

  .slider-container .swiper-slide.product--short .product__button--big {
    width: 110px;
    flex-shrink: 1;
  }

  .slider-container .swiper-slide.product--short .product__button--big span {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}

@media (max-width: 375px) {
  .slider-container .swiper-slide {
    width: calc(100vw - 10px);
    min-width: 280px;
  }
}