.view-types {
	&__type {
		cursor: pointer;

		&--active.svg-icon {
			filter: url(#white) url(#red);
		}
	}
}

.product {
	height: 350px; //TODO
	max-width: 465px;

	&__wrapper {
		position: relative;
		width: 100%;
		overflow: hidden;
		background-color: #1b1a1a;
		border-radius: 3px;
		box-shadow: 0 0 4.5px 0.5px rgba(126, 37, 37, 0.2);
	}

	&:hover {
		.product__wrapper {
			z-index: 20;
			box-shadow: 0 0 13.5px 1.5px rgba(126, 37, 37, 0.5);
		}

		.product__hidden-description {
			display: block;
		}
	}

	&--long {
		max-width: 100%;
		width: 100%;
		max-height: 250px;
		height: 100%;

		.product {
			&__wrapper {
				display: flex;
				max-height: 250px;
			}

			&__image-wrapper {
				max-width: 50%;
			}

			&__main-part {
				max-width: 50%;
				width: 100%;
				display: flex;
				flex-direction: column;
				padding-top: 20px;
				justify-content: space-between;
			}

			&__title {
				order: 1;
			}

			&__hidden-description {
				order: 2;
				display: block;
				margin: 10px auto;
				overflow: auto;
				border: none;
				mask-image: linear-gradient(to top, transparent 0%, black 20%);

				p:last-of-type {
					padding-bottom: 15px;
				}
			}

			&__price-and-buttons {
				order: 3;
				margin: 0;
			}
		}
	}

	&--flex {
		flex: 1 1 350px;
	}

	&__link {
		cursor: pointer;
	}

	&__image-wrapper {
		max-width: 465px;
		width: 100%;
		height: 250px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		background-color: #fff;
		border-radius: 10px; //TODO костыль
		mask-image: radial-gradient(ellipse 40% 40%, #1b1a1a, 80%, transparent);
	}

	&__image {
		max-width: 100%;
		max-height: 100%;
	}

	&__main-part {
		padding: 0 20px 20px 20px;
	}

	&__title {
		font-size: 16px;
		color: #f9f9f9;
	}

	&:not(.swiper-slide) &__title {
		@include trim(2);
	}

	&__price-and-buttons {
		display: flex;
		align-items: center;
		margin-top: 12px;
	}

	&__rating {
		font-size: 14px;
		color: #f9f9f9;
	}

	&__price {
		align-self: flex-end;
		margin-left: auto;
		margin-right: 14px;
	}

	&__button {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		border-radius: 3px;

		&--small {
			width: 25px;
			height: 25px;

			&--grey {
				background-color: #bfbfbf;
			}

			&--red {
				background-color: #da0b29;
			}
		}

		&--big {
			width: 110px;
			height: 25px;
			background-color: #da0b29;
			font-weight: 700;
			color: #1b1a1a;

			justify-content: space-evenly;

			@media (max-width: 425px) {
				width: 25px;

				span:nth-of-type(2) {
					display: none;
				}
			}
		}

		&:not(:last-child) {
			margin-right: 10px;
		}
	}

	&__hidden-description {
		display: none;
	}
}

.product-info {
	margin-top: 20px;
	padding-top: 20px;
	border-top: 1px solid #212020;

	&__field {
		font-size: 14px;

		&:not(:last-child) {
			margin-bottom: 13px;
		}
	}

	&__title {
		color: #807979;
		font-weight: 400;
	}

	&__value {
		color: #bfbfbf;
	}
}
